import axios, { eventsApiURL } from '../../../axios';
import React from 'react'
import { Container } from 'react-bootstrap';
import { useForm } from 'react-hook-form';
import './User.scss'
import { useRef } from 'react';
import { useState } from 'react';
const User = () => {
    const { register, handleSubmit } = useForm();
    const [dragActive, setDragActive] = useState(false);
    // ref
    const inputRef = useRef(null);
    const [handleFiles, setHandleFiles] = useState("nothing")
    // handle drag events
    const handleDrag = (e) => {
        e.preventDefault();
        e.stopPropagation();
        if (e.type === "dragenter" || e.type === "dragover") {
            setDragActive(true);
        } else if (e.type === "dragleave") {
            setDragActive(false);
        }
    };

    // triggers when file is dropped
    const handleDrop = (e) => {
        e.preventDefault();
        e.stopPropagation();
        setDragActive(false);
        if (e.dataTransfer.files && e.dataTransfer.files[0]) {
            // handleFiles(e.dataTransfer.files);
        }
    };

    // triggers when file is selected with click
    const handleChange = (e) => {
        console.log(e.target);
        // e.preventDefault();
        // if (e.target.files && e.target.files[0]) {
        //     setHandleFiles(e.target.files);
        // }
    };

    // triggers the input when the button is clicked
    const onButtonClick = () => {
        inputRef.current.click();
    };
    const onSubmit = (data) => {
        return new Promise((resolve, reject) => {
            const fileReader = new FileReader();
            fileReader.readAsDataURL(data.image[0]);
            fileReader.onload = () => {
                resolve(fileReader.result);
                const image64 = fileReader.result.replace('data:image/jpeg;base64,', '').replace('data:image/png;base64,', '').replace('data:image/jpg;base64,', '')
                console.log(" ~ file: User.jsx:15 ~ returnnewPromise ~ image64", image64)
                axios.post(eventsApiURL, {
                    health: "",
                    rule: "",
                    person: "",
                    deviceid: "safetybrain-website",
                    lng: 0.0,
                    lat: 0.0,
                    image: image64,
                }).then((res) => {
                    console.log(res);
                }).catch((err) => {
                    console.log(err);
                })
            };
            fileReader.onerror = (error) => {
                reject(error);
            };
        });
    };
    return (
        <div className='User'>
            <div className="pages">
                <Container>
                    <div className="upload">
                        {/* <form onSubmit={handleSubmit(onSubmit)}>
                            <input type="file" {...register("image")} />
                            <button type="submit">Send</button>
                        </form> */}
                        <form id="form-file-upload" onDragEnter={handleDrag} onSubmit={handleSubmit(onSubmit)}>
                            <input ref={inputRef} type="file" id="input-file-upload" multiple={true} onChange={handleChange} {...register("image")} />
                            <label id="label-file-upload" htmlFor="input-file-upload" className={dragActive ? "drag-active" : ""}>
                                <div>
                                    <p>{handleFiles}</p>
                                </div>
                            </label>
                            <button className="upload-button" onClick={onButtonClick}>Send</button>
                            {dragActive && <div id="drag-file-element" onDragEnter={handleDrag} onDragLeave={handleDrag} onDragOver={handleDrag} onDrop={handleDrop}></div>}
                        </form>
                    </div>
                </Container>
            </div>
        </div>
    )
}

export default User
